import './App.css';
import React from 'react';
import HomePage from "./pages/HomePage";
import VolumeWorkPage from "./pages/VolumeWorkPage";
import OthersVolumeWorkPage from "./pages/OthersVolumeWorkPage";
import {
    Switch,
    Route,
} from "react-router-dom";

function App() {
    return (
        <Switch>
            <Route exact path="/">
                <HomePage />
            </Route>
            <Route exact path="/VolumeWorkPage">
                <VolumeWorkPage />
            </Route>
            <Route exact path="/OthersVolumeWorkPage">
                <OthersVolumeWorkPage />
            </Route>
        </Switch>
    );
}

export default App;
