import '../../App.css';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import {Link} from 'react-router-dom';
import {IconButton} from "@material-ui/core";
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import MenuIcon from '@material-ui/icons/Menu';
import 'date-fns';
import SectionBlockVolume from "../../components/SectionBlockVolume";
import Employee1 from '../../images/Employee1.png';
import Employee2 from '../../images/Employee2.png';
import Employee3 from '../../images/Employee3.png';
import Employee4 from '../../images/Employee4.png';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100%",
        backgroundColor: 'rgb(255,255,255)',
    },

    hrUp: {
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        width: "85%",
    },

    hrDown: {
        margin: "20px auto",
        borderTop: "1px solid rgba(0, 0, 0, 0.1)",
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        height: 0,
        maxWidth: "45%",
        [theme.breakpoints.down('600')]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up('xs')]: {
            maxWidth: "80%",
        },
        [theme.breakpoints.up('md')]: {
            maxWidth: "60%",
        },
        [theme.breakpoints.up('lg')]: {
            maxWidth: "40%",
        },
    },

    UpperBlock: {
        background: 'rgb(22,111,255)',
        padding: 15,
        borderBottomRightRadius: 50,
    },

    header: {
        fontSize: 24,
        fontWeight: "bold",
        color: 'rgb(255,255,255)',
    },

    title: {
        fontSize: 15,
        fontWeight: "bold",
        textAlign: "left",
    },

    subtitle: {
        fontSize: 12,
        fontWeight: "bold",
        color: 'rgb(113,113,113)',
        textAlign: "left",
    },

    date_text: {
        fontSize: 12,
        color: 'rgb(113,113,113)',
        textTransform: 'uppercase',
    },

    date_value: {
        fontSize: 18,
        color: 'rgb(113,113,113)',
        fontWeight: "bold",
    },

}));

function App() {

    const classes = useStyles();

    return (
        <div className="App">
            <header className="App-header">
                <React.Fragment>
                    <div className={classes.root}>
                        <Grid container direction="column" className={classes.UpperBlock}>
                            <Grid container justifyContent="space-between">
                                <Grid item>
                                    <Link to="/">
                                        <IconButton>
                                            <ArrowBackIosIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                        </IconButton>
                                    </Link>
                                </Grid>
                                <Grid item>
                                    <IconButton aria-label="delete">
                                        <MenuIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                    </IconButton>
                                </Grid>
                            </Grid>
                            <hr className={classes.hrUp}/>
                            <Grid item><div className={classes.header}>Набор работ на Июль 2021</div></Grid>
                        </Grid>
                        <Grid container justifyContent="center" alignItems="center" direction="column">
                            <SectionBlockVolume
                                title="Вертикальные конструкции 2-го этажа на отм. +7,900 hэт.=2950 мм арм. 162 кг/м3"
                                subtitle="Устройство железобетонных стен, толщиной 200 мм"
                                progress="100"
                                date_begin="09.07.2021"
                                date_end="15.07.2021"
                                quantity="24"
                                imgsArray={[
                                    Employee1,
                                    Employee2,
                                ]}
                            />
                            <SectionBlockVolume
                                title="Плита перекрытия тех. этажа на отм. +7,900 арм. 111 кг/м3"
                                subtitle="Устройство перекрытий безбалочных толщиной: 180 мм"
                                progress="58"
                                date_begin="09.07.2021"
                                date_end="15.07.2021"
                                quantity="36"
                                imgsArray={[
                                    Employee1,
                                    Employee2,
                                ]}
                            />
                            <SectionBlockVolume
                                title="Плита перекрытия тех. этажа на отм. +7,900 арм. 111 кг/м3"
                                subtitle="Устройство перекрытий безбалочных толщиной: 200 мм"
                                progress="25"
                                date_begin="09.07.2021"
                                date_end="15.07.2021"
                                quantity="4"
                                imgsArray={[
                                    Employee1,
                                    Employee2,
                                ]}
                            />
                            <SectionBlockVolume
                                title="Вертикальные конструкции с 3-го по 6-ой этажи"
                                subtitle="Устройство железобетонных стен, толщиной 200 мм"
                                progress="3"
                                date_begin="16.07.2021"
                                date_end="26.07.2021"
                                quantity="9"
                                imgsArray={[
                                    Employee3,
                                    Employee4,
                                ]}
                            />
                        </Grid>
                    </div>
                </React.Fragment>
                <CssBaseline />
            </header>
        </div>
    );
}
export default App;
